<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-config-tunjangan-lembur-comp-subsidiary',
  data () {
    return {
      config: {
        title: 'Konfigurasi Subsidi Lembur',
        model_api: 'config-tunjangan-lembur-comp-subsidiary',
        getter: 'config-tunjangan-lembur-comp-subsidiary',
        setter: 'config-tunjangan-lembur-comp-subsidiary',
        pk_field: null,
        permission: {
          create: 'create-config-tunjangan-lembur-comp-subsidiary',
          read: 'view-config-tunjangan-lembur-comp-subsidiary',
          update: 'update-config-tunjangan-lembur-comp-subsidiary',
          delete: 'delete-config-tunjangan-lembur-comp-subsidiary'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Keterangan', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'koefisien', label: 'Koefisien', methods: { list: true, detail: true, create: { type: 'number' }, update: { type: 'number' }, filter: false } },
          {
            id: 'salary_component_id',
            label: 'Komponen Gaji',
            methods: {
              create: {
                setter: 'master-salary-components',
                getter: 'master-salary-components',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name', 'rel_salary_component_category_id']
                  }
                }
              },
              update: {
                setter: 'master-salary-components',
                getter: 'master-salary-components',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-salary-components',
                getter: 'master-salary-components',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



